.cont{
    max-width: 230px;
    width: 70%;
    margin: 10px;
    padding:30px 20px 10px 20px;
    background-color: rgba(240, 248, 255,0.5);
    backdrop-filter: blur(4px);
    box-shadow: 0 8px 32px 0 rgba(163, 163, 163, 0.3);
    border-radius: 15px;
}
.form{
    display: flex;
    flex-direction: column;
}
label{
  color: rgb(50, 50, 50);
}
.form span{
  margin-bottom: -16px;
  color: rgb(255, 99, 71);
  font-size: small;
}
input[type=checkbox] {
    position: absolute;
    left: 0;
    cursor: pointer;
    width: 0px;
    height: 0px;
  }
  
  input[type=checkbox]:checked:before {
    content: "";
    display: block;
    position: absolute;
    width: 17px;
    height: 17px;
    border: 2px solid #2DD4BF;
    border-radius: 15px;
    background-color: #ffffff;  
    transition: all 0.2s linear;
  }
  
  
  input[type=checkbox]:before {
    content: "";
    display: block;
    position: absolute;
    width: 17px;
    height: 17px;
    border: 2px solid #2DD4BF;
    border-radius: 3px;
    background-color: #ffffff;
  }
  
  
  input[type=checkbox]:after {
    content: "";
    display: block;
    width: 0px;
    height: 0px;
    border: solid #2DD4BF;
    border-width: 0 0px 0px 0;
    -webkit-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    transform: rotate(180deg);
    position: absolute;
    top: 0px;
    left: 50px;
    transition: all 0.2s linear;
  }
  
  input[type=checkbox]:checked:after {
    content: "";
    display: block;
    width: 6px;
    height: 11px;
    border: solid #2DD4BF;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
    position: absolute;
    top: 1px;
    left: 6px;
  }
input[type=text],input[type=password]{
    height: 1.7rem;
    border-radius: 5px;
    border: 0.4px solid rgb(221, 221, 221);
    transition: none;
}
input[type=text]:focus,
input[type=password]:focus,
input[type=text]:focus-within,
input[type=password]:focus-within
{
    border: 0.4px solid #2DD4BF;
    outline: solid #2DD4BF;
}
.form button{
    user-select: none;
    color: #fff;
    font-size:large;
    font-weight: bold;
    padding: 3px;
    background-color: #2dd4bf;
    border: none;
    border-radius: 5px;
}
.form a{
    user-select: none;
    margin-top: 10px;
    color: #2DD4BF;
    font-size: large;
    /* font-weight: bold; */
}
.form h1{
    margin-bottom: 10px;
}
.form label{
    text-align: left;
}